const api = require('../../config/api.js');
const util = require('../../utils/util.js')
const pay = require("../../services/pay.js")
const app = getApp()	
export default {
	data() {
		return {
			curr_id: 0,
			navList: [
				{
					id: 0,
					name: '全部'
				},{
					id: 1,
					name: '待付款'
				},{
					id: 2,
					name: '待发货'
				},{
					id: 3,
					name: '待收货'
				},
				{
					id: 4,
					name: '已完成'
				}
			],
			orderList: [],
			params: {
				page: 1,
				size: 100,
				status: 0,
				stime: "",
				etime: ""
			},
			logisicList: [],
			logDialogs: false,
			pay_status: 0,
			payDialogs: false,
			order_id: ''
		}
	},
	//初始化
	onLoad() {
		let pages = getCurrentPages();
		let { options } = pages.pop();
		if(options.id) {
			this.params.status = options.id;
			this.curr_id = options.id;
			this.getList();
		}else {
			this.getList();
		}
		
	},
	// 页面显示
	onShow() {
		
	},
	//分享
	onShareAppMessage() {
		wx.showShareMenu({
			withShareTicket:true
		})
		let code = wx.getStorageSync('code');
		this.setCode(code)
		return {
			title: '@你：这里每天更新好多免费的抽奖，快来抽奖吧~',
			path: '/pages/user/index?code='+code,
			imageUrl: '../../static/images/shareImage.jpg'
		}
	},
	//下拉刷新
	onPullDownRefresh(e){
		setTimeout(function () {
			wx.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		//获取订单列表
		getList() {
			wx.showLoading()
			util.request(api.getOrderList,this.params, 'post').then(res=>{
				wx.hideLoading()
				if(res.code == 0){
					this.orderList = res.data;
				}
			})
		},
		//待点击
		switchCate(id) {
			if(this.params.status == id) {
				return false;
			}
			this.params.status = id;
			this.curr_id = id;
			this.getList()
			
		},
		//选择支付
		payTabClick(status) {
			if(status == 0) {
				this.pay_status = 0;
			}else {
				this.pay_status = 1;
			}	
		},
		//立即支付
		orderLottery() {
			console.log(this.order_id)
			if(this.pay_status == 0) {
				//走微信支付
				// pay.payOrder(order_id).then(res=>{
				// 	console.log(res)
				// })
			}else if(this.pay_status == 1) {
				//走余额支付
				this.balancePay(this.order_id)
			}	
		},
		//余额支付
		balancePay(id) {
			wx.showLoading({
				mask: true,
				title: '订单提交中...'
			})
			//"0192023a7bbd73250516f069df18b500" admin123
			util.request(api.balancePay,{id: id, pwd: "0192023a7bbd73250516f069df18b500"},'post').then(res=>{
				wx.hideLoading();
				if(res.code == 0) {
					//成功之后
					wx.reLaunch({
						url: '/pages/pay/result?status=1&id='+id
					})
				}else {
					util.showErrorToast(res.msg)
				}
			})
		},
		//去付款
		payOrders(order_id) {
			this.payDialogs = true;
			this.order_id = order_id;
		},
		//物流跟踪
		showLogistic(order_no) {
			this.logDialogs = true;
			this.showLogisticAjax(order_no)
		},
		
		//查看物流ajax
		showLogisticAjax(order_no) {
			util.request(api.getLogisicList,{order_no: order_no},'post').then(res=>{
				if(res.code == 0){
					this.logisicList = res.data.list;
				}
			})
		},
		
		//确认收货
		confirmReceipt(order_id) {
			var _self = this;
			wx.showModal({
				title: '确认收货',
				content: '是否确认收货？',
				success(res) {
					if(res.confirm) {
						_self.confirmReceiptAjax(order_id)
					}
				}
			})
		},
		confirmReceiptAjax(order_id) {
			wx.showLoading({
				mask: true
			})
			util.request(api.getOrderReceived,{order_id: order_id},'post').then(res=>{
				wx.hideLoading();
				if(res.code == 0) {
					this.getList()
				}else {
					util.showErrorToast(res.msg)
				}
			})
		},
		//取消订单
		cancelOrder(order_id) {
			var _self = this;
			wx.showModal({
				title: '取消订单',
				content: '是否取消订单？',
				success(res) {
					if(res.confirm) {
						_self.cancelOrderAjax(order_id)
					}
				}
			})
		},
		cancelOrderAjax(order_id) {
			wx.showLoading({
				mask: true
			})
			util.request(api.setOrderCancel,{order_id: order_id},'post').then(res=>{
				wx.hideLoading();
				if(res.code == 0) {
					this.getList()
				}else {
					util.showErrorToast(res.msg)
				}
			})
		},
		dealPrcture(str) {
			if(!str) return ;
			let newStr = str.split(',');
			return newStr[0]
		},
		//关闭物流信息
		close() {
			this.logDialogs = false;
			this.payDialogs = false;
		},
		//传递分享code进去
		setCode(code) {
			util.request(api.setShareInfo,{code: code},'post').then(res=>{})
		}
	}
}
